"use client";

import React, { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";

function PricingForm() {
  // States for the input fields, calculation result and error message
  const [cost, setCost] = useState("");
  const [margin, setMargin] = useState("");
  const [price, setPrice] = useState<number | null>(null);
  const [error, setError] = useState("");

  // Function to calculate final selling price
  const handleCalculate = () => {
    setError("");
    const costValue = parseFloat(cost);
    const marginValue = parseFloat(margin);

    // Validate numeric values
    if (isNaN(costValue) || isNaN(marginValue)) {
      setError("Por favor, insira valores numéricos válidos para custo e margem.");
      setPrice(null);
      return;
    }

    if (costValue < 0 || marginValue < 0) {
      setError("Os valores devem ser positivos.");
      setPrice(null);
      return;
    }

    // Calculation: final price = cost * (1 + margin/100)
    const calculatedPrice = costValue * (1 + marginValue / 100);
    setPrice(calculatedPrice);
  };

  const handleReset = () => {
    setCost("");
    setMargin("");
    setPrice(null);
    setError("");
  };

  return (
    <div className="space-y-6">
      <div className="flex flex-col">
        <Label htmlFor="cost" className="mb-2 text-sm font-medium">
          Custo do Produto (R$)
        </Label>
        <Input
          id="cost"
          type="number"
          step="0.01"
          min="0"
          placeholder="Ex: 50.00"
          value={cost}
          onChange={(e) => setCost(e.target.value)}
          aria-invalid={!!error}
        />
      </div>

      <div className="flex flex-col">
        <Label htmlFor="margin" className="mb-2 text-sm font-medium">
          Margem de Lucro (%)
        </Label>
        <Input
          id="margin"
          type="number"
          step="0.1"
          min="0"
          placeholder="Ex: 30"
          value={margin}
          onChange={(e) => setMargin(e.target.value)}
          aria-invalid={!!error}
        />
      </div>

      {error && (
        <div className="text-sm text-red-600 bg-red-50 dark:bg-red-950/20 p-3 rounded-md border border-red-200 dark:border-red-800">
          {error}
        </div>
      )}

      <div className="flex gap-3">
        <Button onClick={handleCalculate} className="flex-1">
          Calcular Preço
        </Button>
        <Button onClick={handleReset} variant="outline" className="flex-1">
          Limpar
        </Button>
      </div>

      {price !== null && (
        <div className="mt-6 rounded-lg border border-green-200 dark:border-green-800 p-4 bg-green-50 dark:bg-green-950/20">
          <div className="text-center">
            <span className="block text-sm font-medium text-green-800 dark:text-green-200 mb-1">
              Preço de Venda Sugerido
            </span>
            <span className="block text-2xl font-bold text-green-900 dark:text-green-100">
              R$ {price.toFixed(2)}
            </span>
            {cost && margin && (
              <div className="mt-3 text-xs text-green-700 dark:text-green-300 space-y-1">
                <div>Custo: R$ {parseFloat(cost).toFixed(2)}</div>
                <div>Margem: {parseFloat(margin).toFixed(1)}%</div>
                <div>Lucro: R$ {(price - parseFloat(cost)).toFixed(2)}</div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default PricingForm;
